    #wpcot form [type="checkbox"] {
      position: absolute;
      left: -9999px;
      /* checked mark aspect changes */
      /* accessibility */ }
      #wpcot form [type="checkbox"] + label {
        position: relative;
        padding-left: 1.95em;
        cursor: pointer;
        /* checkbox aspect */
        /* checked mark aspect */ }
        #wpcot form [type="checkbox"] + label::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 1.25em;
          height: 1.25em;
